#movieFullTabContainer{
    z-index: 100000000;
    display: none;
}

#fullscreenSurroundingContainer{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1001;
    position: fixed;
    top: 0;
    left: 0;
}

.escape_container{
    color: white;
    position: fixed;
    left: 87.5%;
    top: 8.5%;
    font-size: 25px;
    font-weight: bold;
    cursor: pointer;
}

#fullscreenTabContainer{
    position: fixed; /* Sit on top of the page content */
    display: block; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5); /* Black background with opacity */
    z-index: 1000;
}
#fullscreenContent{
    width: 80%;
    height: 85%;
    overflow: auto;
    background-color: #1C1D23;
    border-radius: 10px;
    z-index: 1000;
}

#movieInfoContainer{
    display: flex;
    padding-left: 30px;
    padding-top: 20px;
}

#movieImageContainer {
    width: 350px;
    margin-right: 20px;
}

#movieImageContainer img{
    width: 350px;
    height: auto;
    border-radius: 10px;
}

#movieFullInfoContainer{
    color: white;
    font-size: 16px;
}

#movieFullInfoTitle{
    font-size: 30px;

    font-family: Bold;
}
.movieFullInfoText{
    margin-top: 15px;
}

#secondGenre{
    margin-top: 0;
}

#fullGenreContainer{
    display: flex;
    margin-top: 15px;
    align-items: center;
}

#movieFullRatingsContainer{
    display: flex;
    align-items: center;
    margin-top: 15px;
}

#fullMovieInfoOverviewContainer{
    width: 80%;
    margin-top: 15px;
    color: rgba(255, 255, 255, 0.212);
}

#movieFullInfoButtonsContainer{
    display: flex;
    margin-top: 20px;
}

.movieFullInfoLikeButton{
    width: fit-content;
    height: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 8px;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
}

.movieFullInfoLikeButton img {
    width: 20px;
    height: 20px;
    filter: invert(17%) sepia(8%) saturate(711%) hue-rotate(195deg) brightness(60%) contrast(92%);
    transition: all 0.1s;
}

.movieFullInfoLikeButtonLiked img {
    filter: invert(32%) sepia(96%) saturate(1349%) hue-rotate(331deg) brightness(96%) contrast(99%);
}

.movieFullInfoPlayButton{
    width: fit-content;
    height: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 8px;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    margin-right: 10px;
}

.movieFullInfoPlayButton img {
    width: 20px;
    height: 20px;
    filter: invert(97%) sepia(44%) saturate(1276%) hue-rotate(62deg) brightness(90%) contrast(85%);

}

.movieFullInfoPlayButton p {
    margin-right: 10px;
    color: white;
}



.movieFullInfoLikeButton, .movieFullInfoPlayButton{
    transition: all 0.2s ease;
    font-size: 17px;
}

.movieFullInfoLikeButton:hover, .movieFullInfoPlayButton:hover{
    background-color: rgba(255, 255, 255, 0.12);
}



@media only screen and (max-width: 1000px) {
    #fullMovieInfoOverviewContainer{
        width: 90%;
    }

    #movieImageContainer {
        width: 250px;
        margin-right: 20px;
    }
    
    #movieImageContainer img{
        width: 250px;
        height: auto;
        border-radius: 10px;
    }
}

@media only screen and (max-width: 850px) {
    #fullscreenContent{
        width: 93%;
        height: 80%;
    }

    #movieInfoContainer{
        flex-direction: column;
        padding-left: 0;
        padding-top: 0;
        align-items: center;
        padding-top: 20px;
    }

    #movieFullInfoContainer{
        display: contents;
    }

    .movieFullMetricsContainer{
        display: flex;
        align-items: center;
    }

    .movieFullInfoText{
        margin-top: 5px;
    }

    .movieFullInfoRelease{
        margin-left: 25px;
    }

    #fullGenreContainer{
        margin-top: 5px;
        margin-left: 25px;
    }

    #movieFullRatingsContainer{
        margin-top: 5px;
        margin-left: 25px;
    }

    #movieFullInfoTitle{
        margin-top: 10px;
        text-align: center;
    }

    .escape_container{
        left: 91.5%;
        top: 11%;
    }
}

@media only screen and (max-width: 750px) {

}